.products {
    display: flex;
    flex-wrap: wrap;
  }

  .product-card {
    display: flex;
    flex-direction: column;
  border: 1px solid brown;
  margin: 2%;
  padding: 2%;
    flex: 1 16%;
    background-color: #fff;
    box-shadow: 0px 0px 1px 0px rgba(0, 0, 0, 0.25);
  }
  
  .product-image img {
    width: 100%;
  }
  
  .product-info {
    margin-top: auto;
    padding-top: 20px;
    text-align: center;
  }
  
  @media (max-width: 920px) {
    .product-card {
      flex: 1 21%;
    }
  
    .products .product-card:first-child,
    .products .product-card:nth-child(2) {
      flex: 2 46%;
    }
  }
  
  @media (max-width: 600px) {
    .product-card {
      flex: 1 46%;
    }
  }
  
  @media (max-width: 480px) {
    h1 {
      margin-bottom: 20px;
    }
  
    .product-filter {
      flex-direction: column;
    }
  
    .sort {
      align-self: flex-start;
    }
  }
  